const relationList = [{
  id: 1,
  name: '',
  option: ['>', '<', '>=', '<=', '', '', '', ''],
  value: 'input'
},
{
  id: 2,
  name: '',
  option: ['', '', '', ''],
  value: 'input'
},
{
  id: 3,
  name: '',
  option: ['>', '<', '>=', '<=', '', '', '', ''],
  value: 'date'
},
{
  id: 4,
  name: '',
  option: ['', ''],
  value: 'select'
},
{
  id: 5,
  name: '',
  option: ['', ''],
  value: 'input'
},
{
  id: 6,
  name: '',
  option: [''],
  value: 'radio'
}
]
export function getOption (id) {
  let option = []
  relationList.map(item => {
    if (id === item.id) { option = item.option }
  })
  return option || []
}
export function getFormType (id) {
  let type
  relationList.map(item => {
    if (id === item.id) { type = item.value }
  })
  return type
}
