<!--  -->
<template>
  <div class="search-lists-com">
    <!--  -->
    <div class="header-t">
      <el-radio-group v-model="model"
                      @change="searchMode"
                      size="medium">
        <el-radio-button label="0"></el-radio-button>
        <el-radio-button label="1"></el-radio-button>
      </el-radio-group>

      <!--  -->
      <el-select size="medium"
                 v-model="timeOrder"
                 style="margin-left:20px;width:130px"
                 @change="sortSelect"
                 placeholder="">
        <el-option label=" "
                   value="1">
        </el-option>
        <el-option label=" "
                   value="0">
        </el-option>
      </el-select>
    </div>

    <p class="search-msg">
        
        <span>{{ patients }}</span> 
        <span>{{ caseHistory }}</span> 
    </p>

    <!-- / -->
    <div style="margin-bottom:30px;">
      <component :is="whichCom"></component>
    </div>

    <!--  -->
    <el-pagination @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="pageNo"
                   :page-sizes="[10, 20, 30, 40, 50]"
                   :page-size="pageSize"
                   :pager-count="5"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="total"
                   background></el-pagination>

  </div>
</template>

<script>

import { mapGetters, mapMutations, mapActions } from 'vuex'
import caseModel from './CaseModel' // 
import patientsModel from './PatientsModel' // 

export default {
  name: 'searchListsCom',
  data () {
    return {
      model: 0, // 
      timeOrder: '', // 
      patients: 0, //  
      caseHistory: 0, // 
      whichCom: 'caseModel', // 
      total: 0 // 
    }
  },
  computed: {
    ...mapGetters(['keyWordSearchCon', 'keyWordListsDatas', 'pageNo', 'pageSize'])
  },
  watch: {
    keyWordListsDatas (val) { //  
      if (val) {
        this.patients = val.personNumber
        this.caseHistory = val.visitNumber
        this.total = val.total
      }
    }
  },
  components: {
    caseModel, patientsModel
  },
  created () { },
  mounted () { },
  destroyed () {
    this['caseSearch/modelarg'](0) // 
    this['caseSearch/timeorderarg']('') // 
    this['caseSearch/pagenoarg'](1) // 
    this['caseSearch/pagesizearg'](10) // 
  },
  methods: {
    ...mapMutations(['caseSearch/modelarg', 'caseSearch/timeorderarg', 'caseSearch/pagenoarg', 'caseSearch/pagesizearg']),
    ...mapActions(['caseSearch/keyWordListsData']),

    // 
    searchMode (val) {
      this.$emit('changeLoading', true)
      this['caseSearch/modelarg'](Number(val))
      this['caseSearch/pagenoarg'](1)
      this['caseSearch/pagesizearg'](10)
      this.getSearchListsData()
      switch (Number(val)) {
        case 0:
          this.whichCom = 'caseModel'
          break
        case 1:
          this.whichCom = 'patientsModel'
          break
      }
    },

    // 
    sortSelect (val) {
      this.$emit('changeLoading', true)
      this['caseSearch/timeorderarg'](Number(val))
      this['caseSearch/pagenoarg'](1)
      this['caseSearch/pagesizearg'](10)
      this.getSearchListsData()
    },

    // 
    getSearchListsData () {
      const data = this.keyWordSearchCon
      this['caseSearch/keyWordListsData'](data).then(res => {
        this.$emit('changeLoading', false)
        if (res) {}
      }).catch(() => {
        this.$emit('changeLoading', false)
      })
    },

    //  
    handleSizeChange (val) {
      this.$emit('changeLoading', true)
      this['caseSearch/pagenoarg'](1)
      this['caseSearch/pagesizearg'](Number(val))
      this.getSearchListsData()
    },

    //  
    handleCurrentChange (val) {
      this.$emit('changeLoading', true)
      this['caseSearch/pagenoarg'](Number(val))
      this.getSearchListsData()
    }

  }
}
</script>

<style lang="scss" scoped>
@import "~@/styles/mixin.scss";
.search-lists-com {
  margin-top: 15px;
  .header-t {
    padding-left: 8px;
  }
  .search-msg {
    padding: 10px 10px;
    color: #2b2b2b;
    span {
      color: #ec6941;
    }
  }
}
</style>
<style lang="scss">
.search-condation-com {
  .el-scrollbar__wrap {
    overflow-x: hidden !important;
  }
}
</style>
