<!--   -->
<template>
<div class="scrollbar-wraps"
       v-loading="loading"
       element-loading-text="..."
       element-loading-spinner="el-icon-loading"
  >
    <el-scrollbar style="height:100%" ref="scroll">
      <div class="search-store-com">
        <ul class="lists-wrap">
          <li class="lists-items pointer"
              v-for="(item,index) in listsData"
              :key="index"
              @click="goJumpKeyWords(item)"
          >
            <!-- -->
            <div class="lists-items-title"
                :class="{'bg-blue': item.searchType === 1 ? true : false,
                          'bg-purple':item.searchType === 2 ? true : false,
                          'bg-green':item.searchType === 3 ? true : false,
                      }"
            >
              {{item.searchTypeName}}
            </div>

            <!--  -->
            <div class="lists-items-star">
              <el-dropdown trigger="click" class="space" ref="messageDrop">
                <span class="pointer"
                      @click.stop="starClick(item)">
                  <svg-icon
                      icon-class="shoucang1"
                      style="color:#0070f4;width:14px;height:14px;"
                  />
                </span>
                <el-dropdown-menu slot="dropdown">
                  <div class="dropdown-checkbox">
                    <el-input
                      size="mini"
                      placeholder=""
                      clearable
                      prefix-icon="el-icon-search"
                      v-model="search"
                      @input="conditionFilter"
                    ></el-input>
                    <el-checkbox-group
                        v-model="checkList"
                        class="block"
                        @change="handleChecked"
                        style="height:150px;width:150px;">
                      <el-scrollbar style="height:100%">
                        <el-checkbox class="ellipsis"
                          :title="items.categoryName"
                          v-for="(items,index) in checkOp"
                          :label="items.id"
                          :key="index"
                        >
                          {{items.categoryName}}
                        </el-checkbox>
                      </el-scrollbar>
                    </el-checkbox-group>
                    <div class="dropdown-button">
                      <el-button
                          type="primary"
                          size="mini"
                          @click.native.stop="sureBtn(item,index)"
                      >
                        
                      </el-button>
                      <el-button
                          size="mini"
                          @click.native.stop="cancel(index)"
                      >
                        
                      </el-button>
                    </div>
                  </div>
                </el-dropdown-menu>
              </el-dropdown>
            </div>

            <!--  -->
            <div class="lists-items-info">
                {{item.searchTerms}}
            </div>

            <!-- / -->
            <div class="lists-items-info2">
              <span class="sn-l">
                 {{ item.personCount }} ,{{ item.noteCount }}
              </span>
              <span class="sn-r">{{ item.searchTime | timeFormat }}</span>
            </div>
          </li>
        </ul>

        <!-- -->
        <p class="reminder"
           v-if="loadingData">
          <span class="pointer"
                @click.stop="getMoreData"> {{ total }} ...</span>
        </p>
        <!-- -->
        <p class="reminder" v-if="noMore"></p>
        <p class="no-data" v-if="noData"></p>
      </div>
    </el-scrollbar>
    <!--  -->
    <div class="clear-btn pointer"
         @click="clearStoreSearch">
      <span></span>
    </div>
</div>
</template>

<script>

import { mapGetters, mapMutations } from 'vuex'
import {
  searchCollection,
  searchColleSure,
  searchColleAlldel
} from '@/api/caseSearch' //
const PinyinMatch = require('pinyin-match') //

export default {
  name: 'SearchStoreCom',
  data () {
    return {
      loading: false,
      pageNo: 1, // 
      pageSize: 10, // 

      categoryIds: '', // 
      total: 0, // 
      loadingData: false, // 
      noMore: false, // 
      noData: false, // 
      listsData: [], // 
      search: '', // 
      checkList: [], // 
      checkOp: [] // 
    }
  },
  props: {
    // 
    selectVal: {
      type: Number,
      default: null
    },
    // 
    checkOptions: {
      type: Array
    }
  },

  watch: {
    // 
    selectVal (val) {
      this.categoryIds = val
      this.listsData = []
      this.noData = false
      this.noMore = false
      this.loadingData = false
      this.pageNo = 1
      this.getSearchCollection()
    }
  },

  computed: {
    ...mapGetters(['userInfo', 'theme'])
  },

  components: {},
  created () {},
  mounted () {
    this.$nextTick(() => {
      this.getSearchCollection() // 
    })
  },
  destroyed () {},
  methods: {
    ...mapMutations(['caseSearch/keywordsinput']),

    // 
    getSearchCollection () {
      this.loading = true
      const data = {
        pageNo: this.pageNo,
        pageSize: this.pageSize,
        categoryIds: this.categoryIds !== null ? this.categoryIds : ''
      }

      searchCollection(data)
        .then(res => {
          const obj = res.obj
          if (obj) {
            if (obj.data.length > 0) {
              this.noData = false
              this.total = obj.total
              this.checkList = []

              // this.listsData = this.listsData.concat(obj.data)
              this.listsData.push(...obj.data)

              if (this.listsData.length >= this.total) {
                this.loadingData = false
                this.noMore = true
              } else {
                this.loadingData = true
                this.noMore = false
              }
              this.scrollWay() // 
            } else {
              this.loadingData = false
              this.noMore = false
              this.noData = true // 
            }
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    // 
    getMoreData () {
      this.pageNo += 1
      this.getSearchCollection()
    },

    // 
    scrollWay () {
      const dom = this.$refs.scroll.wrap
      this.$nextTick(() => {
        dom.scrollTop = dom.scrollHeight
      })
    },

    // 
    starClick (item) {
      const categoryIds = item.categoryIds.split(',')
      this.checkList = []
      this.checkOp = this.checkOptions
      for (let i = 0; i < this.checkOptions.length; i++) {
        categoryIds && categoryIds.map(item => {
          if (Number(this.checkOptions[i].id) === Number(item)) {
            this.checkList.push(Number(item))
          }
        })
      }
      this.search = ''
    },

    // 
    sureBtn (item, index) {
      const newArr = this.checkList.join()
      const id = item.id
      const data = {
        id,
        categoryIds: newArr
      }

      searchColleSure(data)
        .then(res => {
          const success = res.success
          if (success === 0) {
            this.$refs.messageDrop[index].hide()
            this.$message({
              message: res.msg,
              type: 'success'
            })
            this.pageNo = 1
            this.listsData = []
            this.noData = false
            this.noMore = false
            this.loadingData = false
            this.getSearchCollection()
          }
        })
        .catch(() => {})
    },

    // 
    handleChecked (val) {
      this.checkList = val
    },

    // 
    conditionFilter () {
      const newV = this.checkOptions
      const newVal = newV.map(item => {
        const newItem = item.categoryName.search(this.search) !== -1 ||
                        PinyinMatch.default.match(item.categoryName, this.search)
        if (newItem) return item
      }).filter(item => item)
      this.checkOp = newVal
    },

    // 
    cancel (index) {
      this.$refs.messageDrop[index].hide()
    },

    // 
    goJumpKeyWords (item) {
      switch (Number(item.searchType)) {
        case 1: // 
          this['caseSearch/keywordsinput'](item.searchTerms)
          this.$router.push('KeywordsSearch')
          break
        case 2: // 
          this.$router.push('AdvancedSearch') //
          this.$Storage.localSet('searchJson', item.searchJson)
          break
        case 3: // 
          break
      }
    },

    // 
    clearStoreSearch () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.postSearchStoreAlldel()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },

    // 
    postSearchStoreAlldel () {
      const data = {
        userId: this.userInfo?.pkId
      }
      searchColleAlldel(data)
        .then(res => {
          const status = res.success
          if (status === 0) {
            this.listsData = [] // 
            this.noData = true
            this.loadingData = false
            this.noMore = false
            this.$message({
              type: 'success',
              message: res.msg
            })
          }
        }).catch(() => {})
    }
  }
}
</script>

<style lang="scss" scoped>
.clear-btn{
  color: #0070f4;
  font-size: 14px;
}
.search-store-com {
  position: relative;
  min-height: 200px;
  .lists-wrap {
    .lists-items {
      padding: 10px;
      height: 100px;
      border: 1px solid #e6e6e6;
      position: relative;
      margin-bottom: 10px;
    }

    .lists-items-title {
      width: 80px;
      height: 24px;
      font-size: 12px;
      line-height: 24px;
      border-radius: 2px;
      font-weight: 700;
      text-align: center;
    }

    .lists-items-star {
      position: absolute;
      right: 10px;
      top: 13px;
      span {
        margin: 0 10px;
      }
    }

    .lists-items-info {
      margin: 10px 0;
      font-size: 14px;
      color: #333333;
      overflow: hidden; /*  */
      text-overflow: ellipsis; /*  */
      display: -webkit-box; /* chromebox */
      -webkit-box-orient: vertical; /*  */
      -webkit-line-clamp: 1; /*  */
    }

    .lists-items-info2 {
      font-size: 12px;
      color: #999999;
      overflow: hidden;
      .sn-l{
        float: left;
      }
      .sn-r{
        float: right;
        padding-top: 1px;
      }
    }

    .bg-blue {
      background: rgba(0,112,244,0.3);
      color: #0070f4;
    }
    .bg-purple {
      background: rgba(144,94,255,0.3);
      color: #905eff;
    }
    .bg-green {
      background: rgba(34,172,56,0.3);
      color: #905eff;
    }
  }
  .reminder {
    text-align: center;
    color: #0070f4;
    padding: 10px 0px;
    font-size: 12px;
  }
  .no-data {
    position: absolute;
    top: 200px;
    left: 40%;
    color: #2b2b2b;
    font-size: 18px;
  }
  .loading-icon {
    position: absolute;
    width: 100%;
    top: 200px;
  }
}
</style>
