<!--  -->
<template>
  <div class="search-condation-com">
    <div class="module-container">
      <div class="title header-bar">
        <h2></h2>
        <div class="right">
          <h2 class="pointer h2"
              @click="resetAll"></h2>
        </div>
      </div>

      <!--  -->
      <div class="con-1">
        <div class="scrollbar-wraps">
          <el-scrollbar style="height:100%">
            <!--  -->
            <div class="search-range public-wrap">
              <h4></h4>
              <h4 v-if="rangeSetting"
                  class="reset-b pointer"
                    @click="resetSigle(1)"></h4>
              <p>
                <el-checkbox-group v-model="range">
                  <el-checkbox v-for="(item,index) in findRange"
                               :key="index"
                               @change="rangeSearch"
                               :disabled="item.highLight !== 1 ? true : false"
                               :label="item.indexKey">
                    {{ item.indexName }}
                  </el-checkbox>
                </el-checkbox-group>
              </p>
            </div>

            <!--  -->
            <div class="clinic-type public-wrap">
              <h4></h4>
              <h4 v-if="typeSetting"
                  class="reset-b pointer"
                  @click="resetSigle(2)"></h4>
              <p>
                <el-button type="text"
                           size="mini"
                           :style="{color:typeColor !== 1 ? '#2b2b2b' : '#0070f4'}"
                           @click.native="goSearch('',1,2)"></el-button>
                <el-button type="text"
                           size="mini"
                           :style="{color:typeColor !== 2 ? '#2b2b2b' : '#0070f4'}"
                           @click.native="goSearch('',2,2)"></el-button>
                <el-button type="text"
                           size="mini"
                           :style="{color:typeColor !== 3 ? '#2b2b2b' : '#0070f4'}"
                           @click.native="goSearch('',3,2)"></el-button>
              </p>
            </div>

            <!--  -->
            <div class="clinic-age public-wrap">
              <h4></h4>
              <h4 v-if="ageSetting"
                  class="reset-b pointer"
                  @click="resetSigle(6)"></h4>
              <p>
                <el-tooltip v-for="(item, index) in clinicAge"
                            :key="index"
                            effect="dark"
                            :content="item.indexKey"
                            placement="top-start">
                  <el-button type="text"
                             size="mini"
                             :style="{color:ageColor !== index ? '#2b2b2b' : '#0070f4'}"
                             @click.native="goSearch(item, index, 6)">{{item.indexName}}</el-button>
                </el-tooltip>
              </p>

              <!--  -->
              <p>
                <el-popover placement="bottom-start"
                            width="335"
                            v-model="visible">
                  <!--  -->
                  <div class="range-choose">
                    <div class="range-c">
                      <p style="text-align:center;padding:10px 0;">1~119</p>
                      <el-form :inline="true"
                               :rules="rules"
                               ref="ruleForm"
                               :model="ruleForm">
                        <el-form-item prop="startAge">
                          <el-input style="width:122px;"
                                    :maxlength="3"
                                    placeholder="0"
                                    size="mini"
                                    v-model="ruleForm.startAge">
                            <el-select v-model.number="ruleForm.startUnit"
                                       slot="append"
                                       style="width: 65px;">
                              <el-option label=""
                                         value=""></el-option>
                              <el-option label=""
                                         value=""></el-option>
                              <el-option label=""
                                         value=""></el-option>
                            </el-select>
                          </el-input>
                        </el-form-item>

                        <!-- <el-form-item style="margin-top:-20px;">
                          <span></span>
                        </el-form-item> -->
                        <span style="vertical-align:-25% ;margin-right:10px;"></span>

                        <!--  -->
                        <el-form-item prop="endAge">
                          <el-input style="width:122px;"
                                    :maxlength="3"
                                    size="mini"
                                    placeholder="119"
                                    v-model.number="ruleForm.endAge">
                            <el-select v-model="ruleForm.endUnit"
                                       slot="append"
                                       style="width: 65px;"
                                       placeholder="">
                              <el-option label=""
                                         value=""></el-option>
                              <el-option label=""
                                         value=""></el-option>
                              <el-option label=""
                                         value=""></el-option>
                            </el-select>
                          </el-input>
                        </el-form-item>
                      </el-form>
                    </div>
                  </div>
                  <!--  -->
                  <div style="text-align: center;">
                    <el-button size="mini"
                               @click="visible = false"></el-button>
                    <el-button type="primary"
                               size="mini"
                               @click="submitForm('ruleForm')"></el-button>
                  </div>
                  <el-button type="text"
                             size="mini"
                             slot="reference"
                             :style="{color: ageColor === 999 ? '#0070f4' : '#2b2b2b'}"
                             @click.native="goShow">
                    {{rangeChoose}}
                    <i class="el-icon-arrow-down"></i>
                  </el-button>
                </el-popover>
              </p>

            </div>

            <!--  -->
            <div class="gender public-wrap">
              <h4></h4>
              <h4 v-if="genderSetting"
                  class="reset-b pointer"
                  @click="resetSigle(7)"></h4>
              <p>
                <el-button v-for="(item,index) in genderDatas"
                           :style="{color: genderColor !== index ? '#2b2b2b' : '#0070f4'}"
                           :key="index"
                           type="text"
                           size="mini"
                           @click.native="goSearch(item, index, 7)">
                  {{item}}
                </el-button>
              </p>
            </div>

          </el-scrollbar>
        </div>
      </div>

    </div>
  </div>
</template>

<script>

import { mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  name: 'searchCondationCom',
  data () {
    const validateStartAge = (rule, value, callback) => {
      const type = '^[0-9]*[1-9][0-9]*$'
      const r = new RegExp(type)
      const flag = r.test(value) // flag = true 
      if (!flag) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const validateEndAge = (rule, value, callback) => {
      const type = '^[0-9]*[1-9][0-9]*$'
      const r = new RegExp(type)
      const flag = r.test(value) // flag = true 
      if (!flag) {
        callback(new Error(''))
      } else {
        callback()
      }
    }

    return {
      // 
      range: [], // 
      findRange: [], // 
      findColor: 888, // 
      rangeSetting: false, // 

      // 
      visitType: '', // 
      typeColor: 888, // 
      typeSetting: false, // 

      // 
      visitAge: [], // 
      clinicAge: [
        {
          indexName: '',
          indexKey: '1-28', // [1*//, 28*//]
          indexArr: ['1*', '28*']
        },
        {
          indexName: '',
          indexKey: '28-1',
          indexArr: ['28*', '1*']
        },
        {
          indexName: '',
          indexKey: '1-6',
          indexArr: ['1*', '6*']
        },
        {
          indexName: '',
          indexKey: '7-17',
          indexArr: ['7*', '17*']
        },
        {
          indexName: '',
          indexKey: '18-40',
          indexArr: ['18*', '40*']
        },
        {
          indexName: '',
          indexKey: '41-65',
          indexArr: ['41*', '65*']
        },
        {
          indexName: '',
          indexKey: '65-89',
          indexArr: ['65*', '89*']
        }

      ], // 
      ageColor: 888, // 
      ageSetting: false, // 
      ruleForm: {
        startAge: '', // 
        startUnit: '', // 
        endAge: '', // 
        endUnit: '' // 
      },
      rules: {
        startAge: [
          { required: true, trigger: 'blur', validator: validateStartAge }
        ],
        endAge: [
          { required: true, trigger: 'blur', validator: validateEndAge }
        ]
      },
      visible: false, // 
      rangeChoose: '', // 

      // 
      gender: [], // 
      genderDatas: [], // 
      genderColor: 888, // 
      genderSetting: false // 

    }
  },
  props: {
    findRang: {
      type: Array,
      default: null
    }
  },
  computed: {
    ...mapGetters(['userInfo', 'keyWordSearchCon', 'keyWordConDatas'])
  },
  watch: {
    visible (val) { // 
      if (!val) {
        this.ruleForm = {
          startAge: '', // 
          startUnit: '', // 
          endAge: '', // 
          endUnit: '' // 
        }
        this.$refs.ruleForm.resetFields()
      }
    },
    keyWordConDatas (val) {
      if (val) {
        const obj = this.keyWordConDatas
        this.findRange = obj.rangeList ? obj.rangeList : []
        this.genderDatas = obj.gender ? obj.gender : []
      }
    }
    // findRang (val) {
    //   console.log(val)
    //   if (val) {
    //     this.range = [val[0]]
    //     this.rangearg(this.range)
    //   }
    // }
  },
  components: {},
  created () {
    if (this.keyWordConDatas) {
      const obj = this.keyWordConDatas
      this.findRange = obj.rangeList ? obj.rangeList : []
      this.genderDatas = obj.gender ? obj.gender : []
    }
  },
  mounted () { },
  destroyed () {
    this['caseSearch/rangearg']([])
    this['caseSearch/visittypearg']('')
    this['caseSearch/visitagearg']([])
    this['caseSearch/genderarg']([])
  },
  methods: {
    ...mapMutations(['caseSearch/rangearg', 'caseSearch/keyword', 'caseSearch/source', 'caseSearch/visittypearg', 'caseSearch/visitagearg', 'caseSearch/genderarg', 'caseSearch/pagenoarg', 'caseSearch/pagesizearg']),
    ...mapActions(['caseSearch/keyWordListsData']),
    // 
    resetAll () {
      this.$emit('changeLoading', true)
      // 
      this.range = []
      this.findColor = 888
      this.rangeSetting = false
      this['caseSearch/rangearg'](this.range)

      // 
      this.visitType = ''
      this.typeColor = 888
      this.typeSetting = false
      this['caseSearch/visittypearg'](this.visitType)

      // 
      this.visitAge = []
      this.ageColor = 888
      this.ageSetting = false
      this.rangeChoose = ''
      this['caseSearch/visitagearg'](this.visitAge)

      // 
      this.gender = []
      this.genderColor = 888
      this.genderSetting = false
      this['caseSearch/genderarg'](this.gender)

      this.getSearchListsData() // 
    },

    // 
    resetSigle (num) {
      this.$emit('changeLoading', true)
      switch (num) {
        case 1: // 
          this.findColor = 888
          this.range = []
          this.rangeSetting = false
          this['caseSearch/rangearg'](this.range)
          break

        case 2: // 
          this.typeColor = 888
          this.visitType = ''
          this.typeSetting = false
          this['caseSearch/visittypearg'](this.visitType)
          break

        case 6: // 
          this.ageColor = 888
          this.visitAge = []
          this.rangeChoose = ''
          this.ageRange = false
          this.ageSetting = false
          this['caseSearch/visitagearg'](this.visitAge)
          break

        case 7: // 
          this.genderColor = 888
          this.gender = []
          this.genderSetting = false
          this['caseSearch/genderarg'](this.gender)
          break
      }
      this.getSearchListsData() // 
    },

    // 
    goSearch (item, index, num) {
      this.$emit('changeLoading', true)
      this['caseSearch/pagenoarg'](1)
      this['caseSearch/pagesizearg'](10)
      switch (num) {
        case 1: // 
          // this.findColor = index
          // this.range = item.indexKey
          // this.rangeSetting = true
          break

        case 2: // 
          this.typeColor = index
          this.visitType = item
          this.typeSetting = true
          this['caseSearch/visittypearg'](this.visitType)
          this.getSearchListsData()
          break

        case 6: // 
          this.ageColor = index
          this.visitAge = item.indexArr
          this.ageSetting = true
          this['caseSearch/visitagearg'](this.visitAge)
          this.getSearchListsData()
          break

        case 7: // 
          this.gender = []
          this.genderColor = index
          this.gender.push(item)
          this.genderSetting = true
          this['caseSearch/genderarg'](this.gender)
          this.getSearchListsData()
          break
      }
    },

    //  
    goShow () {
      this.ageColor = 999
      this.ageSetting = true
      this.rangeChoose = ''
      this.visitAge = []
    },

    //   
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        const obj = this.ruleForm
        if (valid) {
          this.$emit('changeLoading', true)
          this['caseSearch/pagenoarg'](1)
          this['caseSearch/pagesizearg'](10)
          this.visitAge = [obj.startAge + '*' + obj.startUnit, obj.endAge + '*' + obj.endUnit]
          this.rangeChoose = obj.startAge + obj.startUnit + '' + obj.endAge + obj.endUnit
          this.visible = false
          this['caseSearch/visitagearg'](this.visitAge)
          this.getSearchListsData()
        } else {
          return false
        }
      })
    },

    // 
    rangeSearch () {
      this.$emit('changeLoading', true)
      this['caseSearch/pagenoarg'](1)
      this['caseSearch/pagesizearg'](10)
      this.rangeSetting = true
      this['caseSearch/rangearg'](this.range)
      this.getSearchListsData()
    },

    // 
    getSearchListsData () {
      const data = this.keyWordSearchCon
      this['caseSearch/keyWordListsData'](data).then(res => {
        this.$emit('changeLoading', false)
        if (res) {}
      }).catch(() => {
        this.$emit('changeLoading', false)
      })
    }

  }
}
</script>

<style lang="scss" scoped>
@import "~@/styles/mixin.scss";
.search-condation-com {
  .module-container {
    padding-left: 0px;
    padding-right: 0px;
  }
  .header-bar {
    h2 {
      font-size: 14px;
    }
    height: 35px;
    line-height: 35px;
    padding: 0 15px;
    overflow: hidden;
    background: rgba(43, 134, 178, 0.2);
    .h2 {
      color: #828c92;
      &:hover {
        color: #0070f4;
      }
    }
  }

  .scrollbar-wraps {
    height: calc(100vh - 368px);
  }
  .public-wrap {
    padding: 20px 10px 0px 10px;
    font-size: 14px;
    position: relative;
    .reset-b {
      position: absolute;
      right: 15px;
      top: 20px;
      color: rgb(130, 140, 146);
      &:hover {
        color: #0070f4;
      }
    }
  }
}
.range-choose {
  .range-c {
    padding: 0 10px;
  }
}
.select-s {
  height: 39px;
  line-height: 40px;
  color: #dcdfe6;
  border: 1px solid #dcdfe6;
  option {
    height: 39px;
    border: 1px solid #dcdfe6;
  }
}
</style>
<style lang="scss">
.search-condation-com {
  .el-scrollbar__wrap {
    overflow-x: hidden !important;
  }
  .search-range {
    .el-button + .el-button {
      margin-left: 0px;
    }
    .el-button {
      margin-right: 10px;
    }
  }
  .search-range {
    .el-checkbox {
      margin-right: 10px;
      padding: 5px 0px;
    }
    .el-checkbox__label {
      padding-left: 0 !important;
      font-size: 12px;
    }
    .el-checkbox__input {
      display: none;
    }
  }
}
</style>
